import React from 'react';
import './index.css';
import { makeStyles } from '@material-ui/core/styles';
import {Box} from '@material-ui/core';
//import { flexbox } from '@material-ui/system';



const useStyles = makeStyles(theme=>({

root:
{
 width: "90%",
 height:"90%",
 display:"flex",
 position:"relative",
 top:"5%",
 left:"3%",
 flexDirection:"row",
 alignItems:"center",
 color:"white",
 margin:"3px",
 padding:"2px",
 [theme.breakpoints.down("md")]:
		{
			margin:0,
			float:"left",
			fontSize:"1rem",
			display:"flex",
			flexDirection:"column",
		}
}
,
header:
{
width:"35%",
height:"30%",
fontSize:"1.5rem",
margin:"1em",
[theme.breakpoints.down("sm")]:
		{
			margin:0,
			float:"left",
			fontSize:"1rem",
			display:"flex",
			flexDirection:"column",
		}
}
,
whoweare:
{
color:"#DCDCDC",
}
,
are:
{
color:"teal",
}
,
p:
{   
	marginLeft:'100px',
	[theme.breakpoints.down("sm")]:
		{
			textAlign:"center",
			marginLeft:"10px",
		}
}


}))

const Aboutus = () =>
{

	const classes = useStyles();


return(
 <Box className={classes.root}>
 <div className={classes.header}><h1 className="whoweare">WHO WE<span className={classes.are}> ARE?</span></h1></div>
 <p className={classes.p}>We’re a Digital Agency that offers the complete suite of digital services - 
 Branding, Design, Development, 
 Search Engine Optimisation and Digital Marketing.
 We create quality websites and apps. 
 Our websites are bespoke front-ends, 
 the digital faces of businesses.
  Our apps range from peer-to-peer sharing platforms, 
 to business tools, to booking engines.
 Our technology is primarily built in <b>React</b> 
 (extending to <b> React Native</b>). 
 Where the projects involves a <b>Content Management System</b> 
 (CMS) we use <b>WordPress </b>
 and <b> WooCommerce</b>.
 </p>
</Box>
);
}
export default Aboutus;