import React from 'react';
import './index.css';
import { makeStyles } from '@material-ui/core/styles';
import {Box} from '@material-ui/core';

const useStyles = makeStyles(theme=>({

root:
{
 width: "90%",
 height:"90%",
 display:"flex",
 position:"relative",
 left:"3%",
 color:"white",
 flexDirection:"row",
 alignItems:"center",
 margin:"3px",
 padding:"2px",
 [theme.breakpoints.down("md")]:
		{
			margin:0,
			float:"left",
			fontSize:"1rem",
			display:"flex",
			flexDirection:"column",
		}
}
,
header:
{
width:"35%",
height:"30%",
fontSize:"1.5rem",
margin:"1em",
[theme.breakpoints.down("sm")]:
		{
			margin:0,
			float:"left",
			fontSize:"1rem",
			display:"flex",
			flexDirection:"column",
		}
}

,
why:
{
textAlign:"center",
color:"#DCDCDC",
}
,
us:
{

color:"teal",
}
,
wrapperwhy:
{   
	margin:"2em",
}

}))

const WhyDistl = () =>
{
	const classes = useStyles();

	return (
	
	<Box className={classes.root}>
    <div className={classes.header}><h1 className="why">WHY<span className={classes.us}> DISTL?</span></h1></div>
    <div className={classes.wrapperwhy}>
    <h4>Distl is an ideal place to cultivate your craft as a web developer.</h4>
    <ul>
      <li>You’ll be supported by a close-knit team of developers, and directly reporting to Distl’s technical lead.</li>
      <li>You’ll work autonomously, both independently and collaboratively on projects creating websites and applications 
      from scratch.</li>
      <li>Projects constantly vary with lifecycles as short as four weeks, keeping your work fresh and challenging.</li>
      <li>We'll gear you up with your very own Macbook Pro!</li>
    </ul>
    </div>
    </Box>

  
 );
} 
export default WhyDistl;