import React from 'react';
import './index.css';

import { makeStyles } from '@material-ui/core/styles';
import {Box} from '@material-ui/core';


const useStyles = makeStyles(theme=>({

root:
{
 width: "90%",
 height:"90%",
 display:"flex",
 position:"relative",
 top:"45%",
 left:"3%",
 flexDirection:"row",
 color:"white",
 alignItems:"center",
 margin:"3px",
 padding:"2px",
[theme.breakpoints.down("md")]:
		{
			margin:0,
			float:"left",
			fontSize:"1rem",
			display:"flex",
			flexDirection:"column",
		}
			,

}
,
header:
{
width:"35%",
height:"30%",
fontSize:"1.5rem",
margin:"1em",
[theme.breakpoints.down("sm")]:
		{
			margin:0,
			float:"left",
			fontSize:"1rem",
		}
}

,
job:
{
textAlign:"center",
color:"#DCDCDC",
}
,
req:
{
textAlign:"center",
color:"teal",
}
,
wrapperreq:
{   
	margin:"2em",
}

}))



const OurDream = () =>
{
	const classes = useStyles();

	return (

  <Box className={classes.root}>
    <div className={classes.header}><h1 className="job">JOB-<span className={classes.req}>REQUIR-
    MENTS</span></h1></div>
    <div className={classes.wrapperjob}>
    <h3> We’re looking for a team member that has:</h3>
    <ul>
      <li>An eagerness to learn, a passion for building on the web.</li>
      <li>Strong JavaScript skills, even better if these skill extend to React!</li>
      <li>Front-end nous. Confident with CSS3, familiar with SASS, and most importantly - an eye for style!</li>
      <li>(Bonus) Familiarity with PHP.</li>
      <li>(Bonus) Familiarity with Content Management Systems (For example, WordPress)</li>
      <li>(Bonus) Familiarity with noSQL databases (Firebase, MongoDB).</li>
    </ul>
    </div>
    </Box>
 
    );

}

export default OurDream;