import React from 'react';
import './index.css';
import Aboutus from './Aboutus';
import WhyDistl from './WhyDistl';
import OurDream from './OurDream';
import ContactUs from './ContactUs';
//import {Grid} from '@material-ui/core';
//import distl from './distl1.jfif';
import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles ((theme)=>({

 root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },

dividerFullWidth: {
    margin: `3px 0 0 ${theme.spacing(1)}px`,
  },
  dividerInset: {
    margin: `3px 0 0 ${theme.spacing(9)}px`,
  },

}));

const App = () =>
{

   const classes = useStyles();

return (

<div className="container">

<div className="headline">
<h1>Are You The Next Member of Distl?</h1>
</div>

<div className="powerful-intro">
  <h3>We’re looking to expand our outfit from four to five, in the form of an awesome developer!</h3>
</div>

<Aboutus/>     

<Divider className={classes.dividerFullWidth}/>
 <WhyDistl/>
 
 <Divider className={classes.dividerFullWidth}/>
 <OurDream/>

<divider className={classes.dividerFullWidth}/>

 <ContactUs/>

 </div>
  );
	
}

export default App;