import React from 'react';
import './index.css';
//import EmailIcon from '@material-ui/icons/Email';
import {Grid,Card,CardContent} from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme=>({

card:
{
 margin:"1px",
 padding:"1px",
 backgroundColor:"#808080",
 color:"black",
 borderRadius:"12px",
}
,
	[theme.breakpoints.up("md")]:
		{
			float:"left",
			fontSize:"0.7em",
		}
			,

}))




const ContactUs = () =>
{
	const classes = useStyles();

	return  (


<Grid container  justify="center">
 <Grid item xs={12}>
  <Card className={classes.card}>
  <CardContent>
  <div>
    <p>If this sounds like the right place for you, 
    <b>Fork</b> this Pen and style it up! - https://codepen.io/BirdBrain/pen/rovYBo</p>
    
    <p>No rules - complete creative agency. Maybe you'll give it slick corporate vibes, 
    edgy hipster grunge, or out of this world science fiction grandeur.</p>
    <p>Once you're happy with your Pen, send it in an e-mail, along with your CV, portfolio, CodePen, GitHub to 
    <a href="mailto:david@distl.com.au"> david@distl.com.au</a></p>
  </div>
 </CardContent>
 </Card>
 </Grid>
 </Grid>
		);

}

export default ContactUs;